/* Default CSS */
:root {
  --main-bg-color: #252531;
  --main-color: #fafafc;
  --main-dark-color: #737378;
  --main-orange-color: #ffc107;
  --main-black-color: #1e1e28;
}
*, :after, :before {
  box-sizing: border-box;
  -webkit-transition: .2s ease-in-out;
  transition: .2s ease-in-out;
}
html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
address,
big,
cite,
code,
em,
font,
img,
small,
strike,
sub,
sup,
li,
ol,
ul,
fieldset,
form,
label,
legend,
button,
table,
caption,
tr,
th,
td {
  border: none;
  font-size: inherit;
  line-height: inherit;
  margin: 0;
  padding: 0;
  text-align: inherit;
}

body {
  background: #191923;
  font-size: 14px;
  display: swap;
  font-family: 'Poppins', sans-serif;
}
blockquote::before,
blockquote::after {
  content: "";
}
a {
  text-decoration: none;
}
.clear:after,
ul:after {
  content: "";
  display: block;
  clear: both;
}
.hide {
  overflow: hidden;  /* Hide the element content, while height = 0 */
  height: 0;
  max-height: 0;
  opacity: 0;
}
.show {
  height: auto; 
  opacity: 1;
  transition: height 0ms 2000ms, opacity 2000ms 0ms;
}
